<template>
  <div class="order-wrap" style="border: 1px #D9D9D9 solid;
	margin-top: 29px;border-radius: 6px;background-color: #FFFFFF">
    <div class="order-hd">
      <div class="order-hd_lt">
        <div class="order-filter">
          <el-form :inline="true" :model="orderFilter" class="demo-form-inline">
            <el-form-item label="筛选条件">

              <el-select v-model="orderFilter.state" placeholder="全部">
                <el-option label="下单" value="0"></el-option>
                <el-option label="支付成功" value="1"></el-option>
                <el-option label="余额不足" value="2"></el-option>
                <el-option label="订单取消" value="-1"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="order-hd_rt">
        <div class="order-search">
          <el-input placeholder="搜索订单" v-model="orderFilter.name">
            <i slot="suffix" @click="searchList" class="el-input__icon el-icon-search"></i>
          </el-input>
        </div>
        <div>
          <el-button style="margin-left: 10px;">去充值</el-button>
        </div>
      </div>
    </div>
    <div class="order-bd">
      <div class="order-table">

        <el-table :data="tableData" empty-text="老铁，这里还什么都没有" style="width: 97%;margin-left:30px;" border>

          <div slot="empty">
            <img style="margin:15px" src="static/imgs/manage/null2x.png" />
            <div style="margin:15px">老铁，这里还什么都没有~</div>
          </div>

          <el-table-column prop="orderNo" label="订单号" align="center" style="width: 10%">
          </el-table-column>

          <el-table-column prop="title" label="产品" align="center" style="width: 18%">
          </el-table-column>
          <el-table-column prop="createTime" label="创建时间" align="center" style="width: 24%">
          </el-table-column>
          <el-table-column prop="price" label="金额" align="center" style="width: 18%">
          </el-table-column>
          <el-table-column prop="payTypeName" label="支付方式" align="center" style="width: 10%">
          </el-table-column>
          <el-table-column prop="statusName" label="订单状态" align="center" style="width: 10%">
          </el-table-column>
          <el-table-column label="操作" align="center" style="width: 10%">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="showOrderDetail(scope.$index, tableData)" v-if="tableData[scope.$index].status==1">
                查看
              </el-button>

              <el-button type="text" size="small" @click="showOrderDetail(scope.$index, tableData)" v-if="tableData[scope.$index].status==0">
                支付
              </el-button>

              <el-button type="text" size="small" @click="showOrderDetail(scope.$index, tableData)" v-if="tableData[scope.$index].status==2">
                充值
              </el-button>

              <el-button type="text" size="small" v-if="tableData[scope.$index].status==-1" @click="showOrderDetail(scope.$index, tableData)">
                再次购买
              </el-button>

              <el-button type="text" size="small" v-if="tableData[scope.$index].status==0||tableData[scope.$index].status==2" @click="showOrderDetail(scope.$index, tableData)">
                取消
              </el-button>
            </template>
          </el-table-column>

        </el-table>

      </div>
    </div>
    <div class="order-ft">
      <div class="order-table_pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="listData.count">
        </el-pagination>
      </div>
    </div>
    <div class="order-detail" v-show="orderDetailDialog">
      <el-dialog title="订单详情" :visible.sync="orderDetailDialog" center>
        <!--
				<div slot="empty" title="订单详情">
					
				</div>
				-->
        <div class="order-baseInfo">
          <el-form label-width="100px" :model="list">
            <el-form-item label="订单编号：" prop="orderNo">
              <div class="pos-l">
                <div class="baseInfo-title">{{currentOrder.orderNo}}</div>
                <div class="baseInfo-text">
                  <span>创建时间：{{currentOrder.createTime}}</span>
                  <span>|</span>
                  <span>支付时间：{{currentOrder.payTime}}</span>
                </div>
              </div>

            </el-form-item>
            <el-form-item label="订单状态：" label-position="left" prop="status">
              <div class="pos-l">
                <img :src="status.img[currentOrder.status]">
                <span class="describe">{{status.describe[currentOrder.status]}}</span>
              </div>

            </el-form-item>
            <el-form-item label="支付方式：" prop="payType">
              <div class="pos-l">
                <span>{{currentOrder.payType}}</span>
              </div>

            </el-form-item>
          </el-form>
        </div>
        <el-table :data="[currentOrder]" border>
          <el-table-column property="productName" prop="title" label="名称" width="150"></el-table-column>
          <el-table-column property="money" prop="price" label="价格" width="100"></el-table-column>
          <el-table-column property="amount" prop="count" label="数量" width="200"></el-table-column>
          <el-table-column property="expirationTime" prop="payDataExpire" label="到期时间" width="200"></el-table-column>
        </el-table>
        <div class="order-handle" v-if="currentOrder.status!=1">
          <el-button>{{status.button[currentOrder.status]}}</el-button>
          <el-button @click="orderDetailDialog=!orderDetailDialog">取消</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import User from 'src/server/user'
import utils from 'src/lib/utils.js'
export default {
  // <<<<<<< .mine
  data () {
    return {
      orderFilter: {
        type: '',
        state: '',
        name: ''
      },
      index: 0,
      listData: null, // 订单数据
      // 根据订单状态所显示的相应信息和图片
      status: {
        img: {
          '1': 'static/imgs/manage/complete@2x.png',
          '2': 'static/imgs/manage/complete2@2x.png',
          '0': 'static/imgs/manage/complete3@2x.png',
          '-1': 'static/imgs/manage/complete1@2x.png'
        },
        payType: {
          '1': '余额支付',
          '2': '支付宝支付',
          '3': '其他'
        },
        describe: {
          '1': '订单已完成',
          '2': '余额不足',
          '0': '等待支付',
          '-1': '已取消'
        },
        button: {
          '1': '',
          '-1': '再次购买',
          '0': '确认支付',
          '2': '去充值'
        }
      },
      // 表格数据
      tableData: [],
      currentOrder: {}, // 当前操作的订单
      // 当前表格页码
      currentPage: 1,
      orderDetailDialog: false, // 订单详情对话框的状态
      pageSize: 10 // 一页显示的条数
    }
  },
  methods: {
    handleSizeChange (val) {
      // 改变每页显示的条数
      console.log(`每页 ${val} 条`)
      // this.pageSize=val;
      let params = {
        pageSize: val
      }

      User.list({
        data: params
      }).then(res => {
        if (res.suc && res.code === 200) {
          console.log('page的值:' + res.rs.page)
          this.listData = res.rs
          this.tableData = res.rs.pageList
          this.dataHandle(this.tableData)
        }
      })
      // console.log('这里是pageSize:' + res.rs.pageSize)
    },
    handleCurrentChange (val) {
      // 页码调整
      console.log('当前页: ' + val)

      let params = {
        page: val
      }

      User.list({
        data: params
      }).then(res => {
        if (res.suc && res.code === 200) {
          console.log('page的值:' + res.rs.page)
          this.listData = res.rs
          this.tableData = res.rs.pageList
          this.dataHandle(this.tableData)
        }
      })
    },

    // 搜索订单
    searchList () {
      let self = this
      let params = {
        /*				productId:self.orderFilter.type,
				status:self.orderFilter.status, */
        keyword: self.orderFilter.name
      }

      User.list({
        data: params
      }).then(res => {
        console.log(res)
        if (res.suc && res.code === 200) {
          this.listData = res.rs
          this.tableData = res.rs.pageList
          this.dataHandle(this.tableData)
        } else {
          // console.log(`当前页: ${val}`)
        }
      })
    },
    showOrderDetail (index) {
      // 显示订单详情
      this.orderDetailDialog = true
      this.currentOrder = this.tableData[index]
    },
    // 数据显示信息处理
    dataHandle (data) {
      data.forEach(item => {
        item.statusName = this.status.describe[item.status]
        item.payTypeName = this.status.payType[item.payType]
        item.createTime = utils.formatDate(new Date(item.createTime))
        item.payTime = utils.formatDate(new Date(item.payTime))
      })
    }
  },
  created () {
    console.log('开始')
    // 获取订单数据

    User.list().then(res => {
      if (res.suc && res.code === 200) {
        console.log(res.rs.page)
        this.listData = res.rs
        this.tableData = res.rs.pageList
        console.log(res.rs)
      }
    })
  },
  mounted () {}
}
</script>

<style lang="scss" scoped>
.order-hd {
  margin-top: 39px;
  display: flex;
  justify-content: space-between;
}
.order-hd_lt {
  margin-left: 38px;
}
.order-hd_rt {
  margin-right: 31px;
  display: flex;
  justify-content: space-between;
}
.order-table_pagination,
.order-handle {
  margin-top: 10px;
  display: flex;
  justify-content: flex-end;
}
.order-wrap {
  .el-button--default {
    color: #fff;
    background-color: #fc4a46;
    border-color: #fc4a46;
  }
  .el-button--text {
    color: #fc4a46;
  }
}
.pos-l {
  position: relative;
  left: 50px;
}
.baseInfo-title {
  font-size: 26px;
  color: #fc5c58;
}
.baseInfo-text {
  font-size: 12px;
  color: #979797;
}
.order-detail {
  .order-baseInfo {
    .describe {
      position: relative;
      left: 12px;
      color: #7bb95b;
    }
  }
}
</style>





